import Link from 'next/link'

const linkStyle = {
    marginRight: '15px'
}

const HeaderLink = props => (
    <li>
        <Link as={`${props.as}`} href={`'/nextjs-tutorial'${props.href}`}>
            <a style={linkStyle}>{props.title}</a>
        </Link>
    </li>
)

export default HeaderLink